import React, { useState } from 'react';
import {Link} from 'react-router-dom';
import './diariotrader.css';

function DiarioTrader() {
  const [trades, setTrades] = useState([]);
  const [formData, setFormData] = useState({
   
    mes: '',
    diaMes: '',
    Hora:'',
    tradeDia: '',
    AtivoOperado:'',
    TipoOperacao:'',
    Setup:'',
    Trigger:'',
    MMA9:'',
    MMA20:'',
    MMA200:'',
    TipoEntrada:'',
    TradeRealizado:'',
    TipoStop:'',
    LocalStop:'',
    GestaoStop:'',
    QualoErro:'',
    Resultado:'',
    ResultadoMonetario:'',
    ResultadoEmPtos:'',
    

    
    // Adicione mais campos conforme necessário
  });

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setTrades([...trades, formData]);
    setFormData({
    
    mes: '',
    diaMes: '',
    Hora:'',
    tradeDia: '',
    AtivoOperado:'',
    TipoOperacao:'',
    Setup:'',
    Trigger:'',
    MMA9:'',
    MMA20:'',
    MMA200:'',
    TipoEntrada:'',
    TradeRealizado:'',
    TipoStop:'',
    LocalStop:'',
    GestaoStop:'',
    QualoErro:'',
    Resultado:'',
    ResultadoMonetario:'',
    ResultadoEmPtos:'',
    Comentarios:'',

      // Limpa o formulário
    });
  };

  return (
    
    <nav className="navbar navbar-expand-lg navbar-light bg-light">
    
    <div className="diario-trader-container diario-fixed-top lg-1">


      <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarsExample02" aria-controls="navbarsExample02" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      
      <div className="collapse navbar-collapse" id="navbarSupportedContent">

          <ul className="navbar-nav nav-underline ms-auto">
            <li className="nav-item">
            <Link to="/home" className="nav-link" aria-current="page" >Home</Link>
            </li>
            <li className="nav-item">
            <Link to="/planejamentotrader" className="nav-link" aria-current="page" >Planejamento Diário</Link>
            </li>
            <li className="nav-item">
            <Link to="/gerenciamentorisco" className="nav-link" aria-current="page" >Gerenciamento de Risco</Link>
            </li>
            <li className="nav-item">
            <Link to="/gerenciamentoemocional" className="nav-link" aria-current="page" >Gerenciamento Emocional</Link>
            </li>
            <li className="nav-item">
            <Link to="/diarioemocional" className="nav-link" aria-current="page" >Diário Emocional</Link>
            </li>
            <li className="nav-item">
            <Link to="/dashboard" className="nav-link" aria-current="page" >DashBoard</Link>
            </li>
            <li className="nav-item">
            <Link to="/" className="nav-link" aria-current="page" >Sair</Link>
            </li>
          </ul>
        </div> 
      
      <br></br>
      <br></br>
    

      <header className="header-text-center me-auto">
        <strong>Diário de Trader</strong>
      </header>

      <h2 className="sub-title-text-center">
        Bem-vindo(a) ao Diário de Trader!
      </h2>

      <p className="home-text-center">
        Aqui você encontrará todas as funcionalidades necessárias para
        planejar, gerenciar e analisar suas operações de day trading.
      </p>

      <br></br>
      <br></br>

    <form onSubmit={handleSubmit} className="diario-form">

    <div className="container text-center"></div>

     <div className="row gy-2 gx-3 align-items-center ">

       {/* Linha 1 */}
       <div className="col-auto">            
       <label htmlFor="mes" className="form-label">Mês</label>
        <input
            type="text"
            className="form-control"
            id="autoSizingInput"
            name="mes"
            value={formData.mes}
            onChange={handleInputChange}
            
            
          />
          </div>
       <div className="col-auto">   
      <label htmlFor="diaMes" className="form-label">Dia do Mês</label>
       <input
            type="date"
            className="form-control"
            id="autoSizingInput"
            name="diaMes"
            value={formData.diaMes}
            onChange={handleInputChange}
            
        />
        </div>
         <div className="col-auto">   
      <label htmlFor="hora" className="form-label">Horário da Operação</label>
        <input
          type="time"
          className="form-control"
          id="autoSizingInput"
          name="hora"
          value={formData.Hora}
          onChange={handleInputChange}
          
        />
        </div>
        <div className="col-auto">   
      <label htmlFor="tradeDia" className="form-label">Trade do dia</label>
        <input
          type="number"
          className="form-control"
          id="autoSizingInput"
          name="tradeDia"
          value={formData.tradeDia}
          onChange={handleInputChange}
          
        />        
        </div>
      </div>

{/* Linha 2 */}
<div className="row gy-2 gx-3 align-items-center ">
  <div className="col-auto">   
        <label htmlFor="AtivoOperado" className="form-label">Ativo Operado</label>
          <input
            type="text"
            className="form-control"
            id="autoSizingInput"
            name="AtivoOperado"
            value={formData.AtivoOperado}
            onChange={handleInputChange}
          
          />
        </div>
        <div className="col-auto">   
        <label htmlFor="TipoOperacao" className="form-label">Tipo de Operação</label>
          <input
            type="text"
            className="form-control"
            id="autoSizingInput"
            name="TipoOperacao"
            value={formData.TipoOperacao}
            onChange={handleInputChange}
          
          />
        </div>
        <div className="col-auto">   
        <label htmlFor="Setup" className="form-label">Setup</label>      
          <input
            type="text"
            className="form-control"
            id="autoSizingInput"
            name="Setup"
            value={formData.Setup}
            onChange={handleInputChange}
          
          />
        </div>
        <div className="col-auto">   
        <label htmlFor="Trigger" className="form-label">Trigger</label>      
          <input
            type="text"
            className="form-control"
            id="autoSizingInput"
            name="Trigger"
            value={formData.Trigger}
            onChange={handleInputChange}
          
          />
        </div>
  </div>
  

{/* Linha 3 */}
<div className="row gy-2 gx-3 align-items-center ">
      <div className="col-auto">   
      <label htmlFor="MMA20" className="form-label">MMA20</label>      
         <input
          type="text"
          className="form-control"
          id="autoSizingInput"
          name="MMA20"
          value={formData.MMA20}
          onChange={handleInputChange}
         
          />
          </div>
          <div className="col-auto">   
      <label htmlFor="MMA200" className="form-label">MMA200</label>      
          <input
            type="text"
            className="form-control"
            id="autoSizingInput"
             name="MMA200"
             value={formData.MMA200}
             onChange={handleInputChange}
             
           />
       </div>
        <div className="col-auto">   
       <label htmlFor="TipoEntrada" className="form-label">Tipo de Entrada</label>      
           <input
             type="text"
             className="form-control"
             id="autoSizingInput"
             name="TipoEntrada"
             value={formData.TipoEntrada}
             onChange={handleInputChange}
           />
        </div>
        <div className="col-auto">   
        <label htmlFor="TradeRealizado" className="form-label">O Trade foi realizado?</label>  
            <input
              type="text"
              className="form-control"
              id="autoSizingInput"
              name="TradeRealizado"
              value={formData.TradeRealizado}
              onChange={handleInputChange}
            />
        </div>
        
        {/* Linha 4 */}

        <div className="row gy-2 gx-3 align-items-center ">

         <div className="col-auto">   
        <label htmlFor="TipoStop" className="form-label">Setup de Stop?</label> 
            <input
              type="text"
              className="form-control"
              id="autoSizingInput"
              name="TipoStop"
              value={formData.TipoStop}
              onChange={handleInputChange}
            />
          </div>
         <div className="col-auto">  
        <label htmlFor="LocalStop" className="form-label">Posição do seu Stop?</label>  
         <input
              type="text"
              className="form-control"
              id="autoSizingInput"
              name="LocalStop"
              value={formData.LocalStop}
              onChange={handleInputChange}
            />
        </div>
          <div className="col-auto">   
        <label htmlFor="GestaoStop" className="form-label">Como foi a gestão do seu Stop?</label> 
            <input
              type="text"
              className="form-control"
              id="autoSizingInput"
              name="GestaoStop"
              value={formData.GestaoStop}
              onChange={handleInputChange}
            />
        </div>
          <div className="col-auto">   
        <label htmlFor="QualoErro" className="form-label">Qual foi o seu maior erro?</label>  
            <input
              type="QualoErro"
              className="form-control"
              id="inlineFormSelectPref"
              name="Setup"
              value={formData.QualoErro}
              onChange={handleInputChange}
            />
        </div>

        
{/* Linha 5 */}
    <div className="row gy-2 gx-3 align-items-center ">
      
         <div className="col-auto">   
         <label htmlFor="ResultadoMonetario" className="form-label">Resultado Monetário?</label>  
            <input
              type="number"
              className="form-control"
              id="autoSizingInput"
              name="ResultadoMonetario"
              value={formData.ResultadoMonetario}
              onChange={handleInputChange}
            />
        </div>
         <div className="col-auto">   
        <label htmlFor="ResultadoEmPtos" className="form-label">Resultado em Pontos?</label> 
            <input
              type="number"
              className="form-control"
              id="autoSizingInput"
              name="ResultadoEmPtos"
              value={formData.ResultadoEmPtos}
              onChange={handleInputChange}             
            />
        </div>
         <div className="col-auto">   
        <label htmlFor="Comentários" className="form-label">Comentários sobre a operação</label>
              <input
              type="textarea"
              className="form-control"
              id="ResultadoEmPtos"
              name="Comentarios"
              value={formData.Comentarios}
              onChange={handleInputChange}
              />
        </div>
      </div>    
     </div>  
        <div className="col-12">

        {/* Adicione mais campos de input conforme necessário */}
    </div>
      </div>

      <button class="btn btn-primary" type="submit">Inserir</button>
    <br></br>
    <br></br>
    <br></br>

      </form>

      <div className="table-table-hover table-bordered">
        <table className="table-secondary">
          <thead>
            <tr>
              <th scope="col">Mês</th>
              <th scope="col">Dia do Mês</th>
              <th scope="col">Hora da Operação</th>
              <th scope="col">Trade do Dia</th>
              <th scope="col">Ativo Operado</th>
              <th scope="col">Tipo de Operação</th>
              <th scope="col">Setup</th>
              <th scope="col">Trigger</th>
              <th scope="col">MMA9</th>
              <th scope="col">MMA20</th>
              <th scope="col">MMA20</th>
              <th scope="col">Qual o Tipo de Entrada?</th>
              <th scope="col">O Trade foi realizado?</th>
              <th scope="col">Setup de Stop</th>
              <th scope="col">Posição do seu Stop?</th>
              <th scope="col">Como foi a gestão do seu Stop?</th>
              <th scope="col">Qual foi o seu maior erro?</th>
              <th scope="col">Qual foi seu resultado?</th>
              <th scope="col">Resultado Monetário</th>
              <th scope="col">Resultado em Pontos</th>
              <th scope="col">Comentários</th>
             

              {/* Outras colunas conforme necessário */}
            </tr>
          </thead>
          <tbody>
            {trades.map((trade, index) => (
              <tr key={index}>
             
                <td>{trade.mes}</td>
                <td>{trade.diaMes}</td>
                <td>{trade.Hora}</td>
                <td>{trade.tradeDia}</td>
                <td>{trade.AtivoOperado}</td>
                <td>{trade.TipoOperacao}</td>
                <td>{trade.Setup}</td>
                <td>{trade.Trigger}</td>
                <td>{trade.MMA9}</td>
                <td>{trade.MMA20}</td>
                <td>{trade.MMA200}</td>
                <td>{trade.TipoEntrada}</td>
                <td>{trade.TradeRealizado}</td>
                <td>{trade.TipoStop}</td>
                <td>{trade.LocalStop}</td>
                <td>{trade.GestaoStop}</td>
                <td>{trade.QualoErro}</td>
                <td>{trade.Resultado}</td>
                <td>{trade.ResultadoMonetario}</td>
                <td>{trade.ResultadoEmPtos}</td>
                <td>{trade.Comentarios}</td>

                {/* Continue mapeando outros campos */}
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
    </nav>
  );
}

export default DiarioTrader;